<template>
  <div class="home-box">
    <p class="demo" @click="toLongText">长文本 DEMO</p>
    <p @click="addfn">点击</p>
    <transition-group name="list">
      <p v-for="item in list" :key="item" class="list-item">
        {{ item }}
      </p>
    </transition-group>
  </div>
</template>

<script>
import { reactive, toRefs, onBeforeMount, onMounted } from 'vue';
import { useRouter } from 'vue-router';
export default {
  name: '',
  setup() {
    const data = reactive({
      list: [1, 2, 3, 4, 5]
    });
    const router = useRouter();
    const toLongText = () => {
      router.push({
        name: 'longText'
      });
    };
    const addfn = () => {
      data.list.unshift('22');
    };
    return {
      ...toRefs(data),
      addfn,
      toLongText
    };
  }
};
</script>
