.list-enter-from,
.list-leave-to {
  opacity: 0;
  transform: translateY(30px);
}
.list-enter-active,
.list-leave-active {
  transition: all 1s ease;
}
.list-leave-active {
  position: absolute;
}
.list-move {
  transition: transform 1s ease;
}

